  --dark-background: #000;
  --brand-color-blue: #c1cbe7;
  --brand-color-pink: #fcd0ba;
}

html {
  scroll-behavior: smooth;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  font-weight: 400;
}

*, :before, :after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

ol {
  margin: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

button {
  cursor: pointer;
  border: none;
  margin: 0;
  padding: 0;
}

textarea {
  resize: none;
}

body {
  color: var(--main-text);
  background-color: var(--main-background);
  margin: 0;
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 22px;
  padding-right: 22px;
}

.tagline {
  background-color: var(--brand-color-blue);
  width: 100%;
}

.tagline-box {
  text-align: center;
  padding-top: 8px;
  padding-bottom: 8px;
  font-size: 12px;
  line-height: 1.5;
  display: flex;
}

.mobile-menu-open {
  width: 32px;
  height: 32px;
  background-color: var(--brand-color-blue);
  border: 0;
  margin-left: auto;
  padding: 0;
}

.burger-icon {
  height: 32px;
  width: 32px;
  fill: var(--dark-background);
}

.navigation-list {
  display: none;
}

.mobile-menu-container {
  padding: 40px;
  position: relative;
}

.mobile-menu {
  z-index: 999;
  width: 100vw;
  height: 100vh;
  background-color: var(--main-background);
  flex-direction: column;
  gap: 8px;
  transition: transform .25s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

.mobile-menu.is-open {
  transform: translateX(0);
}

.mobile-menu-close {
  width: 24px;
  height: 24px;
  background-color: var(--brand-color-blue);
  cursor: pointer;
  transition: background-color var(--timing-function) var(--animation-duration), fill var(--timing-function) var(--animation-duration);
  border: none;
  border-radius: 50%;
  margin-left: auto;
  padding: 4px;
}

.mobile-menu-close:hover, .mobile-menu-close:focus {
  background-color: var(--dark-background);
  fill: #fff;
}

.close-icon {
  width: 8px;
  height: 8px;
}

.mobile-navigation-item:not(:last-child) {
  margin-bottom: 16px;
}

.mobile-navigation-link {
  letter-spacing: .02em;
  text-transform: capitalize;
  transition: color var(--timing-function) var(--animation-duration);
  font-size: 36px;
  line-height: 1.22;
}

.mobile-navigation-link:hover, .mobile-navigation-link:focus, .mobile-navigation-link:active {
  color: var(--brand-color-blue);
}

.mobile-sl-list {
  justify-content: space-around;
  margin-top: auto;
  display: flex;
}

.mobile-sl-icon {
  width: 24px;
  height: 24px;
}

.mobile-sl-link {
  fill: var(--dark-background);
  transition: fill var(--timing-function) var(--animation-duration);
